<template>
  <div>
    <input type="text" v-model="search" class="search" />
    <button class="go" @click="s_song()">搜索</button>
    <div class="main">
      <div class="history">
        <ul class="um" id="ul1">
          <li v-for="item in gequ" class="show" @click="cha(item)" :key="item">{{item}}</li>
        </ul>
      </div>
      <div class="cont">
        <ul id="ul2">
          <li v-for="item1 in sousuo" @click="song_info(item1.id)" :key="item1.id">{{item1.name}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "../axios/instance";
export default {
  name: "ge",
  data() {
    return {
      search: "",
      gequ: [this.$route.query.search],
      sousuo: [],
      info: "",
      zhi: ""
    };
  },
  created() {
    this.s_song1();
  },
  methods: {
    s_song1() {
      axios({
        url:
          "/search?keywords= " + this.$route.query.search /*热门歌单接口地址*/,
        method: "post"
      })
        .then(res => {
          this.gequ.push(this.search);
          this.sousuo = res.data.result.songs;
          //   console.log(res.data)
        })
        .catch(err => {
          console.log(err);
        });
    },
    s_song() {
      axios({
        url: "/search?keywords= " + this.search /*热门歌单接口地址*/,
        method: "post"
      })
        .then(res => {
          this.gequ.push(this.search);
          this.sousuo = res.data.result.songs;
          this.gequ = Array.from(new Set(this.gequ)); //new Set数组去重，但是去重之后是个假数组，需要使用Array.from转换为真数组
          

        })
        .catch(err => {
          console.log(err);
        });
    },
    song_info(item1) {
      this.$router.push({
        path: "/info?id=" + item1
      });
    },
    cha(item) {
      axios({
        url: "/search?keywords= " + item /*热门歌单接口地址*/,
        method: "post"
      })
        .then(res => {
          this.search = item;
          this.sousuo = res.data.result.songs;
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

<style scoped>
.search {
  padding-left: 10px;
  border-radius: 15px;
  width: 65%;
  height: 30px;
  margin-left: 10%;
  margin-top: 8%;
  border: 1px solid rgb(216, 158, 168);
  outline: none;
  font-size: 15px;
  color: rgba(0, 0, 0, 0.719);
}
.go {
  border-radius: 5px;
  display: inline-block;
  margin-left: 4%;
  padding: 5px 10px;
  background: pink;
  color: white;
  border: 2px solid white;
}
.main {
  margin-top: 10px;
  /* background: rgb(80, 109, 148); */
}
.um li:nth-child(2) {
  display: none;
}
.history {
  padding: 0 20px;
  padding: 20px;
  /* background: rgba(128, 207, 9, 0.671) */
}
.cont {
  padding: 10px;
  width: 85%;
  margin: 0 auto;
  border-radius: 20px;
  background: linear-gradient(
    to top,
    rgb(248, 238, 238),
    rgb(255, 227, 233),
    rgb(255, 203, 214)
  );
  box-shadow: 0 10px 16px rgba(218, 111, 111, 0.363);
  /* border: 1px solid black; */
  margin-top: 5px;
  height: 60vh;
  overflow: scroll;
}
.history ul li {
  border: 1px solid rgb(216, 158, 168);
  margin-right: 8px;
  margin-bottom: 13px;
  list-style: none;
  display: inline-block;
  padding: 5px 10px;
  border-radius: 20px;
  background: linear-gradient(to right, rgb(238, 139, 157), rgb(248, 205, 213));
  color: white;
  font-weight: bold;
  box-shadow: 0 15px 10px -10px rgba(0, 0, 0, 0.5), 0 1px 4px rgba(0, 0, 0, 0.3),
    0 0 60px rgba(0, 0, 0, 0.1);
  font-size: 15px;
}
.cont ul li {
  box-shadow: 0 10px 16px rgba(0, 0, 0, 0.233);
  /* background:white; */
  background: linear-gradient(
    to right,
    rgb(255, 245, 246),
    rgb(255, 228, 235),
    rgb(255, 204, 217),
    rgb(255, 183, 201)
  );
  font-weight: bold;
  font-size: 17px;
  color: rgba(250, 95, 123, 0.603);
  overflow: hidden;
  margin: 0 auto;
  padding: 0 10px;
  height: 50px;
  line-height: 50px;
  width: 80%;
  border-radius: 7px;
  list-style: none;
  margin-bottom: 25px;
  border: 1px solid rgb(255, 250, 250);
  animation: turn 1s linear;
}
@keyframes turn {
  0% {
    opacity: 0;
    transform: rotateX(-90deg) translate3d(0, 30px, 0);
  }

  100% {
    opacity: 1;
    -webkit-transform: rotate(0deg) translate3d(0, 0, 0);
  }
}
</style>